  padding:0;
  box-sizing:border-box;
}



* {
  font-size: 100%;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  position: absolute;
  width: 100%;
  height: 100%;
  background:#000;
}


body{
  background:#000;

}

.App {
flex:1 1;
display:flex;
flex-direction:column;
  overflow-x: hidden;
  overflow-y: auto;
-webkit-overflow-scrolling: 'touch';
  background:#000;

}



.Header{
  flex:1 1;
  -webkit-text-fill-color:azure;
  color:azure;
  overflow:hidden;
   background-image: linear-gradient(to bottom ,rgba(0,0,0,.2),rgba(0,0,0,.5),rgba(0,0,0,.8),rgba(0,0,0,1)),url(https://i.pinimg.com/originals/44/6e/3b/446e3b79395a287ca32f7977dd83b290.jpg);
  background-size: cover;
  background-position:center;
}


.Main{
  flex:1 1;
  -webkit-text-fill-color:azure;
  color:azure;
  display: flex;
flex-direction:column;
  justify-content:center;
  align-items:center;
}


.firefly {
  position: fixed;
  left: 50%;
  top: 50%;
  width: 0.4vw;
  height: 0.4vw;
  margin: -0.2vw 0 0 9.8vw;
  -webkit-animation: ease 200s alternate infinite;
          animation: ease 200s alternate infinite;
  pointer-events: none;
}
.firefly::before, .firefly::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transform-origin: -10vw;
}
.firefly::before {
  background: black;
  opacity: 0.4;
  -webkit-animation: drift ease alternate infinite;
          animation: drift ease alternate infinite;
}
.firefly::after {
  background: white;
  opacity: 0;
  box-shadow: 0 0 0vw 0vw yellow;
  -webkit-animation: drift ease alternate infinite, flash ease infinite;
          animation: drift ease alternate infinite, flash ease infinite;
}

