.Links{
  flex:1 1;
  display:flex;
  align-items: center;
  justify-content: center;


  min-height: 100vh;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Open Sans', 'Helvetica Neue', sans-serif;
}


:root {
  --color: rgba(250,250,250,.9);
  --transition-time: 0.5s;
}

.Links-cards-wrapper {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  grid-gap: 4rem;
  padding: 4rem;
  margin: 0 auto;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.Links-card {
  font-family: 'Heebo';
  --bg-filter-opacity: 0.5;
  background-image: linear-gradient(rgba(0,0,0,var(--bg-filter-opacity)),rgba(0,0,0,var(--bg-filter-opacity))), var(--bg-img);
  height: 20em;
  width: 15em;
  font-size: 1.5em;
  color: white;
  border-radius: 1em;
  padding: 1em;
  /*margin: 2em;*/
  text-shadow:0 0 10px #000;
  display: flex;
  align-items: flex-end;
  background-size: cover;
  background-position: center;
  box-shadow: 0 0 5em -1em black;
  transition: all, 0.5s;
  transition: all, var(--transition-time);
  position: relative;
  overflow: hidden;
  border: 10px solid #ccc;
  text-decoration: none;
  font-weight:bolder;
}

.Links-card:hover {
  transform: rotate(0);
   text-shadow:0 0 2px #000;

}

.Links-card h1 {
  margin: 0;
  font-size: 1.5em;
  line-height: 1.2em;
}

.Links-card p {
  font-size: 0.75em;
  font-family: 'Open Sans';
  margin-top: 0.5em;
  line-height: 2em;
}

.Links-card .tags {
  display: flex;
}

.Links-card .tags .tag {
  font-size: 0.75em;
  background: rgba(255,255,255,0.5);
  border-radius: 0.3rem;
  padding: 0 0.5em;
  margin-right: 0.5em;
  line-height: 1.5em;
  transition: all, 0.5s;
  transition: all, var(--transition-time);
}

.Links-card:hover .tags .tag {
  background: rgba(250,250,250,.9);
  background: var(--color);
  -webkit-text-fill-color: rgba(0,0,0,.4);
  color: rgba(0,0,0,.4);
  text-shadow:0 0 2px #000;
}

.Links-card .date {
  position: absolute;
  top: 0;
  right: 0;
  font-size: 0.75em;
  padding: 1em;
  line-height: 1em;
  opacity: .8;
}

.Links-card:before, .Links-card:after {
  content: '';
  transform: scale(0);
  transform-origin: top left;
  border-radius: 50%;
  position: absolute;
  left: -50%;
  top: -50%;
  z-index: -5;
  transition: all, 0.5s;
  transition: all, var(--transition-time);
  transition-timing-function: ease-in-out;
}

.Links-card:before {
  background: #ddd;
  width: 250%;
  height: 250%;
}

.Links-card:after {
  background: white;
  width: 200%;
  height: 200%;
}

.Links-card:hover {
  color: rgba(250,250,250,.9);
  color: var(--color);
  text-shadow:0 0 2px #000;
}



.Links-card-grid-space .num {
  font-size: 3em;
  margin-bottom: 1.2rem;
  margin-left: 1rem;
}

.info {
  font-size: 1.2em;
  display: flex;
  padding: 1em 3em;
  height: 3em;
}

.info img {
  height: 3em;
  margin-right: 0.5em;
}

.info h1 {
  font-size: 1em;
  font-weight: normal;
}

/* MEDIA QUERIES */
@media screen and (max-width: 1285px) {
  .Links-cards-wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 900px) {
  .Links-cards-wrapper {
    grid-template-columns: 1fr;
  }
  .info {
    justify-content: center;
  }
  .Links-card-grid-space .num {
    /margin-left: 0;
    /text-align: center;
  }
}

@media screen and (max-width: 500px) {
  .Links-cards-wrapper {
    padding: 4rem 2rem;
  }
  .Links-card {
    max-width: calc(100vw - 4rem);
  }
}

@media screen and (max-width: 450px) {
  .info {
    display: block;
    text-align: center;
  }
  .info h1 {
    margin: 0;
  }
}
